export default {
	data() {
		return {
			loginText: 'Hi, 您未登录',
			topTitleState: false,
			dataList: [{
				list: [{
					icon: '',
					text: '收藏图片',
					textIcon: '',
					id: 1
				},{
					icon: '',
					text: '收藏图集',
